<script>

	import Home from './Home.svelte';
	import Buttons from './Buttons.svelte';
	import SSRJS from './SSRJS.svelte';

	let component;

	if (location.hash === '') {
		component = Home;
	} else if (location.hash === '#buttons') {
		component = Buttons;
	} else if (location.hash === '#ssrjs') {
		component = SSRJS;
	}

</script>
<style>
    :global(.hljs) {padding:12px 0;}
	:global(.hljs-selector-attr) {color:#fff;}
	:global(.hljs-selector-tag) {color:hsl(215,100%,65%);}

	:global(.heading) {margin-top:24px;}
	:global(.dark) {background:#282c34;}
</style>

<svelte:head>
    <link href="https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;500;600;700&display=swap" rel="stylesheet">
</svelte:head>

<main>
	<svelte:component this={component}/>
</main>
